@font-face {
    font-family: Lato;
    src:url(../../assets/fonts/Lato-Regular.ttf);
}
.slider{
    position: relative;
    height: 75vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.image{
    max-width: 100%;
    height: 600px;
    border-radius: 10px;
}
.right-arrow{
    position: absolute;
    top:50%;
    right: 32px;
    font-size: 3rem;
    color: black;
    z-index: 10;
    cursor: pointer;
    user-select: none;
}
.left-arrow{
    position: absolute;
    top:50%;
    left: 32px;
    font-size: 3rem;
    color: black;
    z-index: 10;
    cursor: pointer;
    user-select: none;
}
.mid_section{
    justify-content: center;
    text-align: center;
    font-family: Lato;
}
.mid_section h1{
    font-weight: 800;
    font-family: Lato;
}
.mid_section h3{
    font-weight: 400;
    font-family: Lato;
}

